import React from 'react';
import { useDispatch } from 'react-redux';
import Button from '../../../components/ui/Button';
import productDemoSlice from '../../../store/slices/products-demo.slice';
import useBEM from '../../../utils/hooks/useBEM';
import ProductDemo from './ProductDemo';
import ProductDemoSelection from './ProductDemoSelection';

interface Props {

}

const ProductDemoCompare : React.FC<Props>  = () =>  {
    // VARIABLES
    const dispatch = useDispatch();
    const [B, E] = useBEM('product-demo-compare');

    // HOOKS

    // METHODS
    const back = () => {
        dispatch(productDemoSlice.actions.disableControls(false));
        dispatch(productDemoSlice.actions.enableCompare(false));
    }

    return (
        <div className={B()}>
            <ProductDemo />  

            <Button 
                className={E('back')}
                outlineType="light"
                text="Back" 
                icon="left-arrow-circle-gray" 
                iconPosition="left" type="secondary" 
                click={back}
            />

            <div className={E('splitter')}></div>

            <div className={E('product-selections')}>
                <ProductDemoSelection/>
                <ProductDemoSelection/>
            </div>
        </div>
    )
}

export default ProductDemoCompare;