import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Button from '../../../components/ui/Button';
import { FAMILIES } from '../../../constants/products.constants';
import productDemoSlice, { getProductDemo } from '../../../store/slices/products-demo.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {

}

const ProductDemoSelection : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('product-demo-selection');
    const { 
        isShowProductDescription, 
        isCompareEnabled,  
        lensFamily
    } = useSelector(getProductDemo);
    const [isOpenSubProducts, setIsOpenSubProducts] = useState(false);
    const dispatch = useDispatch();
    const lensFamilies = FAMILIES;
    const [selectedProduct, setSelectedProduct] = useState(lensFamilies[lensFamily].brands[0].products[0]);

    // HOOKS
    useEffect(() => {
        console.log(lensFamily)

        console.log(lensFamilies[lensFamily].brands[0].products[0]);
    }, [])

    // METHODS
    const showDescription = (isShow: boolean) => {
        dispatch(productDemoSlice.actions.showProductDescription(isShow))
    }

    const showSubProducts = (isShow: boolean) => {
        showDescription(false);
        setIsOpenSubProducts(isShow);
        dispatch(productDemoSlice.actions.disableControls(isShow));
    }
    const subProductModifierClass = () => {
        const classes = [];
        if(isOpenSubProducts) classes.push('open');
        if(isCompareEnabled) classes.push('compare');
        return classes;
    }

    const selectProduct = (product: any) => {
        setSelectedProduct(product);
        showSubProducts(false);
    }

    return (
        <div className={B()}>
            
            {
                !isOpenSubProducts && 
                <div className={E('selected')}>
                    <Button    
                        click={() => showSubProducts(true)}
                        text={selectedProduct.label} 
                        icon="pencil-circle-gray" 
                        type="secondary" 
                        outlined={false}/>

                    { !isCompareEnabled &&
                        <Button 
                        type={isShowProductDescription ? 'primary' : 'secondary'} 
                        icon={`information-${isShowProductDescription ? 'white' : 'blue'}`} 
                        outlined={false} 
                        click={() => showDescription(true)}/>
                    }
                </div>
            }
            

            <div className={E('sub-products', subProductModifierClass())}>                
                {
                    lensFamilies[lensFamily].brands.map( (brand: any) => (
                        brand.products.map((product: any) => (
                            <button 
                                key={product.id} 
                                onClick={() => selectProduct(product)}
                                className={E('product', [selectedProduct.id === product.id ? 'active' : ''])}>
                                    {product.label}
                            </button> 
                        ))
                    ))
                }
                <span onClick={() => showSubProducts(false)}></span>
            </div>

        </div>
    )
}

export default ProductDemoSelection;