import React, { useState } from 'react';
import useBEM from '../../../utils/hooks/useBEM';
import Icon from '../../ui/Icon';
import SubProducts from './SubProducts';

interface Props {
    label: string,
    background: string,
    logo?: string,
    subProducts: any[]
}

const ProductCard : React.FC<Props>  = ({
    label,
    background,
    logo,
    subProducts
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('product-card');
    const [isOpenSubProducts, setIsOpenSubProducts] = useState(false);

    // HOOKS

    // METHODS

    return (
        <li className={B()}>                                        
            <Icon
                className={E('background')}
                name={background} 
                type="image" 
                background={true}/>

            {
                logo && 
                <Icon 
                    className={E('logo')}
                    name={logo} 
                />
            }
            
            
            <div className={E('see-more')} onClick={() => setIsOpenSubProducts(true) }>
                See more <Icon name="eye" type="image"/>
            </div>     

            <SubProducts 
                products={subProducts}
                title={label} 
                open={isOpenSubProducts}
                onClose={() => setIsOpenSubProducts(false)}
            />                                   
        </li>
    )
}

export default ProductCard;