import React from 'react';
import PRODUCTS from '../../../constants/products.constants';
import useBEM from '../../../utils/hooks/useBEM';
import ProductCard from './ProductCard';

interface Props {

}

const ProductSelection : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('product-selection');
    const products = PRODUCTS;

    const listDynamicWidth = `${(products.length * 30) + 1}rem`;

    // HOOKS

    // METHODS

    return (
        <div className={B()}>
            <div className={E('scroll')}>
                <ul className={E('list')} style={{width: listDynamicWidth}}>
                    { products.map( (p,index) => (                                    
                        <ProductCard 
                            key={index}
                            label={p.label}
                            background={p.background}
                            logo={p.logo}
                            subProducts={p.subProducts}
                        />                                    
                    ))}
                </ul>
            </div>
        </div>
    )
}

export default ProductSelection;